html, body { 
    padding: 0; 
    margin:0; 
  
    --dark-color: #222222;
  }
  
  html {
    scroll-behavior: smooth;
  }
  
  body {
    max-width: 100%;
    overflow-x: clip;
  }
  
  div.intro {
    width: 100%;
    height: 100vh;
    position: relative;
  
    padding: 0;
    margin-top: 0;
  
    background-image: url('../Media/RobotRock.png');
    background-repeat: no-repeat;
    background-size: cover;
    background-position: center;
  }
  
  span.title {
    position: absolute;
    top: 6vh;
    left: 4vw;
  }
  
  p, div {
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
  }
  
  p.title {
    color: white;
    mix-blend-mode: difference;
  
    margin: 0;
    padding-bottom: 0;
    font-size: 11vmax;
    
    font-weight: bold;
    line-height: 1;
  
  }
  
  p.subtitle {
    color: white;
    mix-blend-mode: difference;
  
    margin: 0;
    padding-top: 0;
  
    font-size: 3vmax;
    font-weight: bold;
    line-height: 1;
  }
  
  .sideImg {
    background-repeat: no-repeat;
    background-size: cover;
    
    width: 40vw;
    height: 80vh;
  }
  
  #leftImg {
    background-image:
    linear-gradient(to right, rgba(255, 255, 255, 0.4), rgb(255, 255, 255)),
    url('../Media/SipOfLife.png');
    background-position: 20% 50%;
  }
  
  #rightImg {
    background-image:
    linear-gradient(to left, rgba(255, 255, 255, 0.4), rgb(255, 255, 255)),
    url('../Media/SnowyMansion.png');
    background-position:center;
  }
  
  .header {
    display: block;
    margin-left: auto;
    margin-right: auto;
    text-align: center;
  
    font-weight: bold;
    text-decoration:solid;
    font-size: 3vmax;
  }
  
  #about {
    position: absolute;
    right: -10vmax;
    bottom: -10vh;
  
    height: 90vh;
    width: 55vmax;
  
    background-color: rgb(39 39 39 / 53%);
    border-top-left-radius: 55vmax;
  }
  
  #protrait {
    position: absolute;
    right: 12vmax;
  
    top: 0; bottom: 35vh;
    margin: auto;
  
    background-image: url(../Media/Portrait.JPG);
    background-position: 95% 40%;
    background-size: 150%;
  
    width: 18vmax;
    height: 18vmax;
  
    border-radius: 50% 60% 90% 10%/ 90% 60% 50% 10%;
    box-shadow: 3px 5px 0px 3px #0e0e0e;
  }
  
  .faded {
    position: absolute;
    right: 13vmax;
  
    top: 48vh; bottom: 0;
    margin: auto;
  
    max-width: 30vmax;
    padding: 1vh 1vw;
  
    background-color: rgba(0,0,0,.06);
    box-shadow: 0px 0px 20px 12px rgba(0,0,0,.06);
  }
  
  .scroll-reminder {
    position: absolute;
    bottom: -0.5vmax;
    width: 15vmax;
    max-height: 0;
  
    left: 0; right: 0;
    margin: auto;
  
    background-color: var(--dark-color);
    border-radius: 1vw 1vw 0 0;
  
    animation: reminder-popup 0.35s forwards;
    -webkit-animation: reminder-popup 0.35s forwards;
    animation-delay: 5s;
  }
  
  @keyframes reminder-popup {
    0%   {max-height: 0;}
    100% {max-height: 2.5vmax;}
  }
  
  .highlights {
    position: absolute;
  
    left: 2vw;
    bottom: 10vh;
  
    background-color: var(--dark-color);
    border-radius: 0.7vmax;
    padding: 0.5vmax 0.5vmax;
  }
  
  .fa-chevron-left, .fa-chevron-right {
    height: fit-content;
    font-size: 1.7vmax;
    color: inherit;
  
    display: inline-block;
  }
  
  .fa-chevron-left::before, .fa-chevron-right::before {
    vertical-align: middle;
    line-height: 10vmax;
  
    padding-left: 0.3vmax;
    padding-right: 0.3vmax;
  }
  
  .highlight-select {
    border-radius: 0.3vmax;
  
    color: #1d1d1d;
  }
  
  .highlight-select:hover {
    color: #585858;
  }
  
  .highlight-browser {
    height: 10vmax;
    width: 29vmax;
  
    position: relative;
  
    gap: 1vmax;
    overflow: clip;
  }
  
  [class^="highlight-browser-cont"] {
    width: fit-content;
    height: fit-content;
  
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    align-items: center;
  
    gap: 1vmax;
  
    position: absolute;
    top: 0.5vmax;
    bottom: 0.5vmax;
  
    transition: all 0.5s;
  }
  
  .highlight-browser-cont1 {
    left: 0vmax;
  }
  .highlight-browser-cont2 {
    left: -10vmax;
  }
  .highlight-browser-cont3 {
    left: -20vmax;
  }
  
  
  /* socials */
  .socials {
    position: absolute;
    display: flex;
    justify-content: space-between;
    width: 10vmax;
  
    left: 2vw;
    bottom: 2vh;
  }
  
  .fa-brands {
    display: table-cell;
    font-size: 1.5vmax;
    line-height: 3vmax;
    width: 3vmax;
    height: 3vmax;
    text-align: center;
    vertical-align: middle;
    text-decoration: none;
    border-radius: 0.5vmax;
  
    background: #272727;
    color: rgb(220, 236, 238);
  }
  
  .fa-brands:hover {
    color: #272727;
    background: rgb(220, 236, 238);
    font-weight: bold;
  }
  
  /* header */
  div.sticky {
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    background-color: var(--dark-color);
    padding: 10px 40px;
    font-size: 25px;
  
    z-index: 200;
  
    display: flex;
    align-items: center;
    justify-content: space-around;
  }
  
  .header-text {
    color: white;
    cursor: pointer;
  
    margin: 0.5vmax;
    font-size: 30px;
    font-weight: 500;
  
    transition: all 0.5s;
  }
  .header-text:hover {
    text-decoration: underline;
  }
  
  .bottombar {
    display: flex;
    align-items: center;
    justify-content: space-evenly;
    background-color: var(--dark-color);
  }
  
  .bottombar > p {
    color: #cdcdcd;
    font-weight: 500;
  }